import React, {Component} from 'react';
import ApiService from "./ApiService";

import '../css/layout.css';

class ChatComp extends Component{
	textRef = React.createRef();

	constructor(props){
		super(props);
			
        this.state={
			senderId:"customer",
			email:window.localStorage.getItem("userEmail"),
			message:null,
			location:null,
			replies:[
				{
					className:'left',
					senderId:'admin',
					message:'안녕하세요.\n무엇을 도와드릴까요?'
				}
			],
			flag:"normal"
		}
	}
		
	handleKeyPress=(event)=>{
		if(event.charCode === 13){
			this.sendMessage(this.state.message);
			var data={
				flag:this.state.flag,
				senderId:this.state.senderId,
				message:this.state.message,
				location:window.location.href,
				email:this.state.email
			}
			this.receiveMessage(data);
		}
	}
	
	sendMessage=(msg)=>{
		var data={
			senderId:"customer",
			message:msg,
		}
		this.addChatDetail(data, 'right');
	}
	
	addChatDetail=(data, lr)=>{
		
		this.setState({replies:[...this.state.replies,{
			uuid:this.state.replies.length+1,
			className:lr,
			senderId:data.senderId,
			message:data.message
		}]})
	}

	receiveMessage=(data)=>{
		ApiService.sendMsg(data)
			.then(res=>{
				console.log(JSON.stringify(res.data));
				this.addChatDetail(res.data,'left');
				this.clearMessage();
				
				if(res.data.flag == 'complete'){
					var data={
						senderId:"admin",
						message:"또 무엇을 도와드릴까요?",
					}
					this.addChatDetail(data, 'left');
					this.setState({flag:'normal'});
				}else{
					this.setState({flag:res.data.flag});
				}
			})
	}
	
	clearMessage=()=>{
		this.textRef.current.value = '';
		this.state.message = '';
	}
	
	onChange=(e)=>{
		this.setState({[e.target.name]:e.target.value})
	}

	render(){
		return(
            <div className="dimmed_layer_wrapper">
                <div className="full_layer">
                    <div className="common_alert"> 
						<div>
							<button type="button" onClick={this.props.onClose}>close</button>
						</div>
						<div>
	    					<div class="header">
	        					CHAT
						    </div>
						</div>
					    <div class="chat">
					        <ul>{
								this.state.replies.map((e)=>(
									<li class={e.className}>
										<div class="sender">
											<span>{e.senderId}</span>
										</div>
										<div class="message">
											<span>{e.message}</span>
										</div>
									</li>
								))
							}
					        </ul>
					    </div>
					    <div class="input-div">
					        <textarea
								placeholder="Press Enter for send message."
								name="message"
								ref={this.textRef}
								onChange={this.onChange}
								onKeyPress={this.handleKeyPress}/>
					    </div>
	
					    <div class="chat format">
					        <ul>{
								this.state.replies.map((e)=>(
									<li class={e.className}>
										<div class="sender">
											<span>{e.senderId}</span>
										</div>
										<div class="message">
											<span>{e.message}</span>
										</div>
									</li>
								))
							}
					        </ul>
                    	</div>
                	</div>
            	</div>
			</div>
		);
	}
}

export default ChatComp;
